import React             from "react";
import { HashRouter }    from "react-router-dom";
import { Routes }        from "react-router-dom";
import { Route }         from "react-router-dom";

import Header            from "../components/header/Header";
import Footer            from "../components/footer/Footer";

import Home              from "../pages/Home";
import Banco             from "../pages/banco/Banco";
import Comida            from "../pages/comida/Comida";
import Forum             from "../pages/forum/Forum";

const Router = () => {

    return(

    <HashRouter>

        <Routes>

            <Route path='/'         element={<div> <Header /> <Home/> <Footer /> </div> }/>

            <Route path='/banco'    element={<div> <Header /> <Banco/> <Footer /> </div> }/>

            <Route path='/comida'   element={<div> <Header /> <Home/> <Footer /> </div> }/>

            <Route path='/forum'    element={<div> <Header /> <Home/> <Footer /> </div> }/>

        </Routes>

    </HashRouter>
    )

}

export default Router